@import 'src/myHeader';
@import 'src/colors';

.aboutDiv {
    overflow-y: auto;
    overflow-x: hidden;
    background: $mainBackground;
    display: flex;
    flex-direction: row;
    //scroll-behavior: smooth;
    //scroll-snap-type: y mandatory;
}

.test {
    min-height: calc(#{100vh-$headerHeight});
    display: flex;
    justify-content: center;
    align-items: center;

    //background: blue;
}

.test2 {
    display: flex;
    justify-content: center;

    align-items: center;
    min-height: calc(#{100vh-$headerHeight});

    //background: yellow;
}

.aboutContainer {
    overflow-x: hidden;
    display: block;
}

.item2 {
    background: blue;
    height: 90vh;
}

.skewed {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $mainBackground;

    transform: skewY(18deg);
    transform-origin: top right;
}


.playStore{
    
    &__container{
        height: 15vh;
        width: 100%;
        // background: white;
        font-size: 30px;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        text-decoration: none;
        flex-direction: column;

        &__text{
            text-decoration: none;
            color:white;
        }
    }
}